.container{
    display: flex;
    flex-direction: column;
    padding: 10px;
    justify-content: space-between;
    border: 1px solid #b4b4b4;
    border-radius: 10px;
    height: 150px;
    align-items: center;
    margin: 10px auto;
}

@media screen and (min-width: 768px){
    .container{
        display: flex;
        flex-direction: row;
        height: 80px;
    }
}